export default {
  props: ["data"],
  data: function data() {
    return {};
  },
  mounted: function mounted() {},
  methods: {
    opened: function opened() {
      this.getCheckoutKey();
    },
    getCheckoutKey: function getCheckoutKey() {
      var _this = this;

      //获取支付密钥
      this.data.loading = true;
      this.$apiCall('api.User.getCheckoutKey', {}, function (r) {
        _this.data.loading = false;

        if (r.ErrorCode == "9999") {
          _this.data.token = r.Data.Results;

          _this.$nextTick(function () {
            var payButton = document.getElementById("pay-button");
            var form = document.getElementById("payment-form");
            Frames.init(_this.data.token);
            Frames.addEventHandler(Frames.Events.CARD_VALIDATION_CHANGED, function (event) {
              console.log("CARD_VALIDATION_CHANGED: %o", event);
              payButton.disabled = !Frames.isCardValid();
            });
            Frames.addEventHandler(Frames.Events.CARD_TOKENIZED, function (event) {
              //点击
              _this.$emit('payCheckOut', event.token);

              Frames.removeEventHandler(Frames.Events.CARD_TOKENIZED, function (event) {});
              console.log(event.token);
            });
            form.addEventListener("submit", function (event) {
              event.preventDefault();
              Frames.submitCard();
            });
          });
        } else {
          _this.$elementMessage(r.Message, 'error');
        }
      });
    }
  }
};